}
.app-heading i {
  font-size: 36px;
  cursor: pointer;
  position: absolute;
  right: 20px;
}
#logo {
  margin: 0;
  padding-bottom: 10px;
}
.content {
  margin-top: 50px;
}
#main {
  height: 90vh;
  width: 100vw;
  padding: 0;
  overflow: scroll;
  background-color: white;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(237, 243, 237, 0.3);
}
.table {
  margin-bottom: 0;
  border-top: 0;
}
.table-bordered td,
.table-bordered th {
  border-color: #848484;
}
.table thead th {
  border-bottom-color: #848484;
}
th,
td {
  font-size: 12px;
  max-width: 300px;
  overflow: hidden;
  white-space: nowrap;
}
td {
  text-align: center;
}
th.sticky-th-top {
  position: -webkit-sticky;
  position: sticky;
  top: -1px;
  z-index: 10;
  border-top: none;
}
th.sticky-th-bottom {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  z-index: 10;
}
th.sticky-th-left {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  z-index: 9;
  background: white;
}

.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}























.table-white {
  background-color: white;
}
.table-hover tbody tr:hover {
  opacity: 0.8;
}
.table-hover .table-danger:hover {
  background-color: #f5c6cb;
}
.table-hover .table-success:hover {
  background-color: #c3e6cb;
}
.isGold,
.isSilver,
.isBronze {
  padding-left: 20px !important;
}
.isGold {
  color: #ccac00;
}
.isSilver {
  color: #808080;
}
.isBronze {
  color: #d48500;
}
.isGold::before,
.isSilver::before,
.isBronze::before {
  position: absolute;
  left: 0;
}
.isGold::before {
  content: "\1F3C5";
}
.isSilver::before {
  content: "\1F948";
}
.isBronze::before {
  content: "\1F949";
}

#loading-container {
  width: 100%;
  height: 100%;
  height: -webkit-fill-available;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.spinner {
  width: 40px;
  height: 40px;

  position: relative;
  margin: 100px auto;
}
.double-bounce1,
.double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: rgb(51, 151, 68);
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;

  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}
.double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
@-webkit-keyframes sk-bounce {
0%,
  100% {
    -webkit-transform: scale(0);
}
50% {
    -webkit-transform: scale(1);
}
}
@keyframes sk-bounce {
0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
}
50% {
    transform: scale(1);
    -webkit-transform: scale(1);
}
}

